.title {
    font: var(--font-l);
    color: var(--primary-color);
}

.text {
    color: var(--secondary-color);
}

.secondary {
    .text {
        color: var(--third-color);
    }
}

.error {
    .title {
        color: var(--red-light);
    }

    .text {
        color: var(--red-dark);
    }
}
